body {
    *{
        position: relative;
    }
    .top-bar {
        position: fixed;
        top: 0;
        left: 0;
        height: 70px;
        width: 100%;
        background-color: $white;
        display: block;
        align-items: center;
        padding-left: 210px;
        .logo-container {
            position: absolute;
            top: 0;
            left: 0;
            width: 200px;
            height: 100%;
            display: block;
            padding: 10px;
            #logo {
                width: 100%;
                height: 100%;
                background-color: #faf;
            }
        }
        .top-bar-icons-container {
            position: static;
            width: 100%;
            .top-bar-icons {
                display: block;
                float: right;
                margin: 0;
                padding: 0;
                list-style: none;
                .top-bar-icon {
                    float: left;
                    margin-right: 10px;
                    .icon {
                        
                    }
                }
            }
        }
        .top-bar-underline {
            
        }
    }
}

.makieta {
    padding-top: 15px;
    padding-bottom: 15px;
    border: 0.5px solid rgba($black, 0.5);
    background-color: rgba($black, 0.05);
}